
$dark-grey: #383838;
$light-grey: #F4F4F4;
$mid-grey: #BBBBBD;
$light-brown: #DDD1C1;
$dark-brown: rgba(102, 51, 0, .7);

* {
	padding: 0;
	font-family: 'Rubik', sans-serif;
	box-sizing: border-box;
	color: $dark-grey;
	background-color: $light-grey; }


*::placeholder {
	color: $mid-grey; }

.links-container {
	margin-top: 5px; }

.title {
	border-bottom: 1px solid $dark-brown;
	margin-bottom: 1rem;
	display: block;
	font-size: 1.2rem;
	padding-bottom: 30px;
	text-align: center;
	font-weight: 700; }

.center {
	justify-content: center;
	align-items: center;
	display: flex; }

.main-title {
	font-size: 1.3rem;
	font-weight: 700;
	width: 180px;
	text-align: center;
	padding-top: 10px;
	align-self: flex-start;
	margin-right: 80px; }

.search {
	position: fixed;
	padding-left: 20px;
	display: flex;
	top: 0;
	left: 0;
	width: 100vw;
	height: 150px;
	align-content: center;
	align-items: center;
	justify-content: space-between;
	background-color: white; }

.search * {
	background-color: white; }


.container-flex {
	display: flex; }

.main {
	margin-top: 200px;
	padding-top: 30px;
	justify-content: space-around;
	width: 100%; }

.black {
	background-color: black; }

.search-button {
	margin-left: 10px;
	padding: 5px;
	display: inline-block;
	border: 1px solid rgba(0, 0, 0, .1);
	border-radius: 3px;
	flex-shrink: 0;
	background-color: $light-grey; }

a:visited, a:link {
	color: $light-grey; }


.jobs {
	width: 22.5vw; }

.jobs * {
	color: $dark-brown; }

.job * {
	background-color: $light-brown;
	color: $dark-grey; }

.job {
	padding: 6px;
	margin-top: 15px;
	margin-bottom: 15px;
	border-radius: 6px;
	background-color: $light-brown; }


.loading {
	margin-top: 10px; }

.description {
	text-overflow: clip {
  white-space: nowrap;
  overflow: hidden; }
	width: 100%;
	display: block; }

.search-button:hover, .search-button:focus {
	cursor: pointer;
	background-color: $mid-grey; }

.search-button:active {
	color: $dark-grey; }

.job-button {
	box-shadow: 3px 3px 3px $light-grey;
	padding: 2px 8px;
	border-radius: 7px;
	text-decoration: none;
	background-color: $dark-brown;
	color: $light-grey;
	margin-right: 10px; }

.job-button * {
	color: $light-grey; }

.job-button:hover {
	background-color: $dark-grey; }

.search-input {
	margin-right: 10px;
	padding: 5px;
	font-size: 16px;
	flex-shrink: 0;
	border: 1px solid rgba(0, 0, 0, .2); }


input:focus {
  outline: none; }



.search-container {
	margin-left: 20px; }



form {
	display: flex;
	align-items: center;
	justify-content: space-around; }


.buttons-container {
	justify-content: space-around; }

@media (max-width: 780px) {

	.search-container {
		flex-direction: column;
		height: 150px;
		align-content: center;
		justify-content: space-around; }

	.main-title {
		font-size: 1.2rem;
		order: 1;
		width: 100%; }

	.links-container {
		display: flex;
		flex-direction: column; }

	.job {
		font-size: .6rem;
		padding-bottom: 1px; }

	.jobs {
		padding-top: 40px;
		width: 24vw; }

	.title {
		font-size: .8rem;
		height: 2.5rem; }

	.form {
		width: 100%;
		align-self: center;
		order: 2; }

	.job-button {
		box-shadow: 3px 3px 3px $light-grey;
		padding: 2px 8px;
		margin-right: auto;
		margin-top: 4px;
		font-size: .8rem; }


	.search {
		height: 250px;
		justify-content: space-evenly;
		flex-direction: column; }


	.search-input {
		margin: 0;
		width: 100%; }

	.search-button {
		margin: 5px;
		width: auto;
		min-width: 75px;
		flex-shrink: 0; } }



